/* CSS for larger screens */
.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  text-align: center;
}

/* Rest of the CSS code remains the same */

/* CSS for smaller screens */
@media (max-width: 768px) {
  .about-container {
    padding: 10px;
    box-sizing: border-box;
  }
}

.image-container {
  width: 200px;
  height: 200px;
  margin-bottom: 20px;
  overflow: hidden;
}

.image-container img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.mission p {
  font-size: 18px;
  margin: 0;
  font-family: 'Amatic SC', cursive;
  font-family: 'Rampart One', cursive;
  font-family: 'Reem Kufi Ink', sans-serif;
}

.social {
  display: flex;
  justify-content: center;
}

.card {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 0 10px;
  perspective: 1000px;
}

.face {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  transition: transform 0.5s;
}

.face1 {
  background-color: #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
}

.face2 {
  background-color: #f9f9f9;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotateY(180deg);
}

.card:hover .face1 {
  transform: rotateY(180deg);
}

.card:hover .face2 {
  transform: rotateY(0deg);
}

.icon {
  font-size: 48px;
  color: #333;
}

h3 {
  margin-top: 10px;
  margin-bottom: 5px;
  font-family: 'Amatic SC', cursive;
  font-family: 'Rampart One', cursive;
  font-family: 'Reem Kufi Ink', sans-serif;
}

p {
  font-size: 14px;
  color: #000000;
  margin-top: 0;
  margin-bottom: 10px;
  font-family: 'Amatic SC', cursive;
  font-family: 'Rampart One', cursive;
  font-family: 'Reem Kufi Ink', sans-serif;
}

/* CSS for smaller screens */
@media (max-width: 768px) {
  .about-container {
    padding: 10px;
  }

  .image-container {
    width: 150px;
    height: 150px;
  }

  .mission p {
    font-size: 16px;
  }

  .card {
    width: 150px;
    height: 150px;
  }
}

